import { Movie, HtmlPropsForField } from '../../../types/types';

type Props = {
  field: keyof Movie,
  fieldProps: (extraClass?: (string | null)) => HtmlPropsForField;
  hasFieldError: boolean;
};

export const FormField: React.FC<Props> = ({ field, fieldProps, hasFieldError }) => (
  <label className="form__item" htmlFor={field}>
    <span className="form__title">{field}</span>

    {field === 'description' ? (
      <textarea
        {...fieldProps('form__field--textarea')}
      />
    ) : (
      <input
        {...fieldProps()}
        type="text"
      />
    )}

    {hasFieldError && (
      <span className="form__warning">Incorrect URL</span>
    )}
  </label>
);
